import { useState } from "react";
import useReadCSV from "../csv/useReadCSV"
import { Recipe } from "../csv/recipe";
import { GridColDef, DataGrid } from "@mui/x-data-grid";


const columns: GridColDef[] = [
    {field: 'recipe_id', headerName: 'Recipe ID'},
    {field: 'recipe_name', headerName: 'Recipe Name'},
    {field: 'category', headerName: 'Category'},
    {field: 'rating', headerName: 'Rating'},
    {field: 'cook_time', headerName: 'Cook Time'},
    {field: 'servings', headerName: 'Servings'},
    {field: 'category_sub', headerName: 'Sub Category'},
    {field: 'tod', headerName: 'Time of Day'}
]

function Homepage() {
    const [recipes, setRecipes] = useState<Recipe[]>([])

    const rows: {}[] = [];
    
    useReadCSV(process.env.PUBLIC_URL +  '/recipes.csv')
        .then((recipes) => {
            recipes.map(function(recipe,_) {
                rows.push(
                    {
                        recipe_id: recipe.recipe_id,
                        recipe_name: recipe.recipe_name,
                        category: recipe.category,
                        rating: recipe.rating,
                        cook_time: recipe.cook_time,
                        servings: recipe.servings,
                        category_sub: recipe.category_sub,
                        tod: recipe.tod
                    }
                )
            })
            return setRecipes(recipes);
        })

    return(
        <div style={{textAlign: 'center'}}>
            <DataGrid 
                rows={rows}
                columns={columns}
                initialState={{
                    pagination: {
                        paginationModel: {page: 0, pageSize: 5}
                    }
                }}
                pageSizeOptions={[5,10]}
                checkboxSelection
            />
        </div>
    )
}

export default Homepage